import React, { Component } from 'react';
import { PostData } from './../../services/PostData';
//import CSS
//import             './../../../components/css/bootstrap.min.css';
import               './../../../components/css/mdb.lite.css';
import               './../../../components/css/loginPage.css';
//import Images
import Map           from './../../../components/img/Icons_0006_maps-and-flags-(3).png';
import Phone         from './../../../components/img/Icons_0005_phone-receiver-(1).png';
import Envelope      from './../../../components/img/Icons_0004_close-envelope.png';
import Clogo         from './../../../components/img/logo.png';

class Login extends Component {

    constructor(props){
        super(props);
        this.state = {
            email: '',
            password: '',
            deveice_id: '123456'
        }
        this.login  = this.login.bind(this);
        this.onChange = this.onChange.bind(this);
    }

    login(){
        if(this.state.email && this.state.password)
        {
            PostData('login', this.state).then ((result) => {
                if(result.status == 1)
                {
                    this.props.history.push('/dashboard');
                }
            });
            //sessionStorage.setItem('userData', responseJSON); 
        }
        
    }

    onChange(e){
        this.setState({[e.target.name]: e.target.value});
    }

render(){
    return (
        <div>
            <section className="theme-color-bg py-2">
                <div className="container white-text font-weight-500 my-1">
                    <ul className="nav">
                    <li className="mr-4"><img src={Map} className="valign-middle mr-1" />185, 3rd Floor,
                        Zone-I, M.P. Nagar, Bhopal, M.P. 462011</li>
                    <li className="mr-4"><img src={Phone} className="valign-middle mr-1" />97559 96968</li>
                    <li className="mr-4"><img src={Envelope} className="valign-middle mr-2" />info@cybrom.com</li>
                    </ul>
                </div>
            </section>
            <section>
                <div className="streak streak-photo registration-form bgImagesLoginPage" >
                    <div className="flex-center rgba-black-light h-100 py-5">
                    <div className="container">
                        <div className="row justify-content-center">
                        <div className="col-lg-5 text-center py-5">
                            <img src={Clogo} width="300" className="mb-5" />
                            <div className="card">
                            <div className="card-body">
                                <div className="form-header pink">
                                    <h4 className="h4-responsive text-uppercase"><b>Sign into your account</b></h4>
                                </div>
                                <div className="md-form">
                                    <i className="fas fa-envelope prefix"></i>
                                    <input type="text" id="inputIconEx1" name="email" placeholder="Your Student ID" className="form-control" value={this.state.username} onChange={this.onChange} />
                                    {/* <label className="font-weight-500" for="inputIconEx1">Your Student ID</label> */}
                                </div>
                                <div className="md-form">
                                    <i className="fas fa-lock prefix"></i>
                                    <input type="password" id="orangeForm-pass" name="password" placeholder="Your password" className="form-control" value={this.state.password} onChange={this.onChange} />
                                    {/* <label className="font-weight-500" for="orangeForm-pass">Your password</label> */}
                                </div>
                                <div className="text-center mt-5">
                                    <button className="btn btn-pink btn-block font-weight-700 font-16" onClick={this.login}>login</button>
                                </div>
                            </div> 
                            </div>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
            </section>
        </div>
    )
  }
}

export default Login;